import React from 'react'
import PropTypes from 'prop-types'

function Video (props) {
  let restProps = {}
  if (props.autoPlay) {
    restProps.autoPlay = 'autoplay'
  }
  if (props.loop) {
    restProps.loop = 'loop'
  }
  if (props.controls) {
    restProps.controls = 'controls'
  }
  if (props.poster) {
    restProps.poster = props.poster
  }

  return (
    <video
      muted={props.muted}
      src={props.src}
      className={props.className}
      onEnded={props.onEnded}
      style={props.style}
      {...restProps}
    />
  )
}

Video.defaultProps = {
  autoPlay: true,
  loop: true,
  controls: false,
  muted: true,
  poster: ''
}

Video.propTypes = {
  src: PropTypes.string.isRequired,
  poster: PropTypes.string,
  onEnded: PropTypes.func
}

export default Video
